import { useState } from "react";

function Sort(props) {

    const [select, setSelect] = useState('titleAsc');

    const doSelect = (e) => {
        setSelect(e.target.value);
        props.doSort(select);
    };

    return (
        <div className="sorter">
            <select defaultValue={""} onChange={doSelect}>
                <option value="">Rūšiuoti pagal: </option>
                <option value="kainaAsc">Kaina [1-100]</option>
                <option value="kainaDesc">Kaina [100-1]</option>
                <option value="pavAsc">Pavadinimas [A-Z]</option>
                <option value="pavDesc">Pavadinimas [Z-A]</option>
            </select>
        </div>
    );
}

export default Sort