// React imports
import { useState, useEffect, useRef} from "react";
import { useForm } from "react-hook-form";
import './styles.css'

// Vendors (3rd-parties) imports
import axios from 'axios';

// Components imports
import DisplayList from './Components/DisplayList';
import Sort from './Components/SortList';

export default function App() {

    // STATES
    const [todos, setTodos] = useState([]);
    const persistRef = useRef(false);
    const [latest_submit, set_latest_submit] = useState({});
    const [isEditing, setIsEditing] = useState(false);
    const [currentTodo, setCurrentTodo] = useState({});
    const {register, handleSubmit, watch , formState: { errors }  , reset } = useForm();
/*     const [isChecked, set_isChecked] = useState(false);
    const currentCheck = useRef(false);
    const discountField = useRef(false); */

    const forLocalHost = '' ?? 'http://127.0.0.1:8787' ; // replace ?? with || forLocalHost otherwise leave it as is , if prepared for deployment !
  
    // ACTIONS
    const getData = () => axios.get(`${forLocalHost}/view-items`).then((response)=>{
      setTodos(response.data)
      /* setTodos(response.data) */
    });
    const postData = (data) => axios({
      method: 'POST',
      url: `${forLocalHost}/post-item`,
      data
    }).then(/* request=> console.log(request.data) */)

    useEffect(()=>{
      console.log("currently editing (currentTodo): ", currentTodo)
    }, [currentTodo])

    useEffect(()=>{

        if (persistRef.current && postData(latest_submit)) {
            
            console.log("second & later App run")
            console.log("latest_submit [logged from useEffect]: ", latest_submit)
            getData()
        
        }
        else {
            console.log("very first App run")
            persistRef.current = !persistRef.current;
            getData()
            window.localStorage.setItem("is_busy", JSON.stringify(true)) // [USE LSTORAGE FOR DEBUGGING ONLY] :  after page (component) refresh checkbox stays checked 
        }

    }, [latest_submit])

    // SORT
    const doSort = (criteria) => {
      console.log(criteria);
      const todosCopy = todos.slice();
      if (criteria === 'kainaDesc') {
          todosCopy.sort((a,b) => a.input1-b.input1);
          setTodos(todosCopy);
      }
      else if (criteria === 'kainaAsc') {
          todosCopy.sort((a,b) => b.input1-a.input1);
          setTodos(todosCopy);
      }
      else if (criteria === 'pavDesc') {
          todosCopy.sort((a, b) => {

              if (a.input2 > b.input2) {
                  return 1;
              }
              else if(a.input2 < b.input2) {
                  return -1;
              }
              else {
                  return 0;
              }
          });
          setTodos(todosCopy);
      }
      else if (criteria === 'pavAsc') {
          todosCopy.sort((a, b) => {
              if (a.input2 > b.input2) {
                  return -1;
              }
              else if(a.input2 < b.input2) {
                  return 1;
              }
              else {
                  return 0;
              }
          });
          setTodos(todosCopy);
      } 
    }

    // CREATE (ADD)
    const onSubmitAdd = (data) => {
        console.log(data);
        set_latest_submit({...data})
        getData()
        reset();

        window.localStorage.setItem("is_busy", JSON.stringify(true))

    }

    // UPDATE (PUT)
    function handleEditClick(e) {

        setIsEditing(true); // Editing UI form is shown instead regular UI for adding extra items
        
        todos.filter((filteredValue)=>{
          return filteredValue._id == e.currentTarget.id ? setCurrentTodo( filteredValue ) : false
        })
    
    }

    const onSubmitEdit = (e, data) => {

      e.preventDefault()

      let [one, two, three/* , four */] = watch(["inputEdit1", "inputEdit2", "inputEdit3"/* , "inputEdit4" */]);

      let copyId = {...currentTodo}._id; // ignore any warning thrown (if any)
      /* console.log("copyId of currenTodo: ", copyId); */

      setCurrentTodo( { ...currentTodo , input1: one, input2: two, input3: three /* , input4: four  */} );
      
      const todosCopy = todos.slice();
      for (let i = 0; i < todosCopy.length ; i++) {
        /* console.log(todosCopy[i]) */ // this line still confirms non-updated state
          if (todosCopy[i]._id == copyId) {/* console.log(todosCopy[i]._id == copyId) */ // id checked
              todosCopy[i] = { copyId , input1: one, input2: two, input3: three /* , input4: four */ } // this line pretends updating state
              axios.get(`${forLocalHost}/replace-one-by-id/${copyId}/input1/${one}/input2/${two}/input3/${three}`); // this definitely updates the state !
          }
      }
  
      setTodos(/* ()=> */todosCopy);
      setIsEditing(false);
      reset();
      
      getData();
      getData();

    }
  
    // DELETE
    function handleDeleteClick(e) {
    
    const todosCopy = todos.slice(); /* console.log("todosCopy: ", todosCopy) */
    for (let i = 0; i < todosCopy.length ; i++) {
        if (todosCopy[i]._id == e.currentTarget.id) {
        console.log (`deleted object whose id was ${e.currentTarget.id} :`, todosCopy.splice(i, 1) ); // I see you did job, so...
        axios.get(`${forLocalHost}/delete-one-by-id/${e.currentTarget.id}`);
        break; // ... you can terminate the loop !
        }
    }

    setTodos(/* ()=> */todosCopy);

    }
  
  return (
    
      <div className="grid-wrapper">
      <div className="section">
      <div className="article">
      {isEditing ?
      (
        <>  {/* EDIT UI */}
          <div id="edit-ui">
            
            <div style={{margin: "10px", textAlign: "center"}} className="heading"><h1>Knygos aprašas:</h1></div>
            
            <form id="user-form" onSubmit={(e)=>{ handleSubmit( onSubmitEdit(e) ) }} >

            <label htmlFor="inputEdit1">Kaina (&euro;):</label>
            <input type="number" className="form-control" id="inputEdit1" defaultValue={currentTodo.input1} min="1" {...register("inputEdit1", {required: true, pattern: /^[0-9.]+$/i} )} />
            {errors.inputEdit1 && <span style={{color: "red"}}>Laukelis būtinas ir tik skaičiai</span>}

            <label htmlFor="inputEdit2">Knygos pavadinimas:</label>
            <input className="form-control" id="inputEdit2" defaultValue={currentTodo.input2} {...register("inputEdit2", {required: true, pattern: /[A-Za-z_]{1,}/i})} />
            {errors.inputEdit2 && <span style={{color: "red"}}>Laukelis būtinas ir tik raidės</span>}
            
            <label htmlFor="inputEdit3">Išparduodama?</label>
            <select id="inputEdit3" name="inputEdit3" className="custom-select" defaultValue={currentTodo.input3} {...register("inputEdit3")} >
              <optgroup label="Select from: ">
                <option value="taikoma">Išpardavimas</option>
                <option value="netaikoma">Nėra išpardavimo</option>
              </optgroup>
            </select>

            <button className="title-windows95" type="submit" onClick={(e)=>{ handleSubmit( onSubmitEdit(e) ) }}>Atnaujinti</button>
            <button className="title-windows95" onClick={() => setIsEditing(false)}>Atšaukti</button>
            
            </form>
          
          </div>
        </>
      )
      : /* <-- LEAVE COLON AS IS */
    (
    <> {/* ADD UI */}
        <div id="add-ui">
            
            <header id="add-ui--header">
              <div className="heading"><h1>Test deployment , please no phising !</h1></div>
              <hr/>
              <div className="heading"><h1>Pridėk knygą:</h1></div>
              <Sort doSort={doSort} />
            </header>
            
            <form id="user-form" onSubmit={()=>{handleSubmit(onSubmitAdd())}} >

            <label htmlFor="input1">Kaina (&euro;):</label>
            <input type="number" className="form-control" id="input1" defaultValue="1" min="1" {...register("input1", {required: true, pattern: /^[0-9.]+$/i})} />
            {errors.input1 && <span style={{color: "red"}}>Laukelis būtinas ir tik skaičiai</span>}

            <label htmlFor="input2">Knygos pavadinimas:</label>
            <input className="form-control" id="input2" defaultValue="pavadinimas" {...register("input2", {required: true, pattern: /[A-Za-z_]{1,}/i})} />
            {errors.input2 && <span style={{color: "red"}}>Laukelis būtinas ir tik raidinės reikšmės</span>}

            <label htmlFor="input3">Išparduodama?</label>
            <select id="input3" name="input3" className="custom-select" defaultValue="Nėra išpardavimo" {...register("input3")} /* ref={discountField} */>
              <optgroup label="Būsena: ">
                <option value="taikoma">Išpardavimas</option>
                <option value="netaikoma">Nėra išpardavimo</option>
              </optgroup>
            </select>

            {/* <input type="checkbox" name="isAvailable" checked={ isChecked } onChange={handleCheck}/> */}
            
            <input type="submit" value="Pridėti" className="title-hacker" onClick={handleSubmit(onSubmitAdd)} />
            </form>
        </div>

        <div id="view-ui">

            <h3 style={{margin: "10px", visibility: todos.length == 0 ? "hidden" : "visible"}}>Visos knygos:</h3>

            <table id="view-ui--data" /* ref={tableRoot} */>
                      
                      <tr style={todos.length == 0 ? {visibility: "hidden"} : {visibility: "visible"}}>
                        <th>ID</th>
                        <th>Kaina</th>
                        <th>Knygos pavadinimas</th>
                        <th>Taikoma nuolaida</th>
                        <th></th>
                        <th></th>
                      </tr>
                            
                      {

                      todos.map((items, index, thisArray) =>
                        
                        (
                        <DisplayList
                        todo={items}
                        idx={index}
                        onEditClick={handleEditClick}
                        onDeleteClick={handleDeleteClick}
                        />
                        )
                         
                      )

                      }


            </table>
            <hr />
            <h4>Bendras knygų kiekis (vienetais): {
            todos.length
            }
            &nbsp;</h4>
            <h4>Vidutinė vienos knygos kaina: { 
            
            todos.length == 0 ? 0 : ( (todos.reduce((sum, i) => (
              sum += parseFloat(i.input1) == '' || undefined || null ? 0 : parseFloat(i.input1)
              ), 0) ) / todos.length ).toFixed(2)
            
            }&nbsp;&euro;</h4>
        </div>
    </>
    )
  }
      </div>
      </div>
      </div>
); /* return's closing tag ;  */
} /* App component closing tag ; */