export default function TodoItem({todo, idx, onEditClick, onDeleteClick}){
    
    return(
    <>
        <tr key={todo._id}>

            <td>{ ++idx }</td>{/* input001 == ID */}
            <td>{todo.input1}</td>{/* input1 == Kaina */}
            <td>{todo.input2}</td>{/* input2 == Pavadinimas */}
            <td>{todo.input3}</td>{/* input3 == Nuolaidos būsena */}
            <td><button id={todo._id} className="title-glitch" onClick={(e) => onEditClick(e)}>Redaguoti</button></td>
            <td><button id={todo._id} className="title-glitch" onClick={(e) => onDeleteClick(e)}>Trinti</button></td>
        
        </tr>
    </>
    );

}